import { Component, OnInit } from '@angular/core';

import { ApprovalService } from '../../angular-services/approval.service';
import { Approval } from '../../classes/approval';

@Component({
  selector: 'app-list-approvals-admin',
  templateUrl: './list-approvals.component.html',
  styleUrls: ['./list-approvals.component.css']
})
export class ListApprovalsComponent implements OnInit {

  approvals: Approval[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private approvalService: ApprovalService) { }

  ngOnInit() {
    this.getApprovals();
  }

  getApprovals() {
    this.approvalService.getApprovals().subscribe(
      response => {
        if (response !== undefined) {
          this.approvals = response;
        } else {
          alert('Houve algum erro ao carregar os dados!');
        }
      }
    )
  }

  removeApproval(approval) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.approvals = this.approvals.filter(a => a !== approval);
      this.approvalService.removeApproval(approval._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Aprovação excluído com sucesso!';
            this.alert.show = true;
          } else {
            alert('Houve algum erro ao carregar os dados!');
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
